.de-dyteditor{width:100%;min-height:100%;height:100%;display:flex}.de-dyteditor .de-ide{flex:1 1;min-height:100%;height:100%;display:flex;flex-direction:column}.de-dyteditor .de-preview{flex:1 1;height:100%;overflow-y:auto}
/* PrismJS 1.24.1
https://prismjs.com/download.html#themes=prism-okaidia&languages=markup+css+clike+javascript */
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */

 code[class*="language-"],
 pre[class*="language-"] {
     color: #f8f8f2;
     background: none;
     text-shadow: 0 1px rgba(0, 0, 0, 0.3);
     font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
     font-size: 1em;
     text-align: left;
     white-space: pre;
     word-spacing: normal;
     word-break: normal;
     word-wrap: normal;
     line-height: 1.5;
 
     -moz-tab-size: 4;
     tab-size: 4;
 
     -webkit-hyphens: none;
     -ms-hyphens: none;
     hyphens: none;
 }
 
 /* Code blocks */
 pre[class*="language-"] {
     padding: 1em;
     margin: .5em 0;
     overflow: auto;
     border-radius: 0.3em;
 }
 
 :not(pre) > code[class*="language-"],
 pre[class*="language-"] {
     background: #272822;
 }
 
 /* Inline code */
 :not(pre) > code[class*="language-"] {
     padding: .1em;
     border-radius: .3em;
     white-space: normal;
 }
 
 .token.comment,
 .token.prolog,
 .token.doctype,
 .token.cdata {
     color: #8292a2;
 }
 
 .token.punctuation {
     color: #f8f8f2;
 }
 
 .token.namespace {
     opacity: .7;
 }
 
 .token.property,
 .token.tag,
 .token.constant,
 .token.symbol,
 .token.deleted {
     color: #f92672;
 }
 
 .token.boolean,
 .token.number {
     color: #ae81ff;
 }
 
 .token.selector,
 .token.attr-name,
 .token.string,
 .token.char,
 .token.builtin,
 .token.inserted {
     color: #a6e22e;
 }
 
 .token.operator,
 .token.entity,
 .token.url,
 .language-css .token.string,
 .style .token.string,
 .token.variable {
     color: #f8f8f2;
 }
 
 .token.atrule,
 .token.attr-value,
 .token.function,
 .token.class-name {
     color: #e6db74;
 }
 
 .token.keyword {
     color: #66d9ef;
 }
 
 .token.regex,
 .token.important {
     color: #fd971f;
 }
 
 .token.important,
 .token.bold {
     font-weight: bold;
 }
 .token.italic {
     font-style: italic;
 }
 
 .token.entity {
     cursor: help;
 }
 
.de-editor_wrapper{width:100%;flex:1 1 auto}.de-editor_wrapper .de-editor{position:relative;width:100%;min-width:100%;min-height:100%;height:100%;max-height:100%;overflow:auto}.de-editor_wrapper .de-editor .de-editor_code{position:absolute;top:0;left:0;min-width:100%;max-width:100%;min-height:100%;height:auto;background:transparent;white-space:pre-wrap;padding:1rem;border:none;word-wrap:break-word;word-break:break-all;font-size:1rem;line-height:1.5em;box-sizing:border-box}.de-editor_wrapper .de-editor .de-editor_code.text{opacity:1;margin:0;background:#272822;resize:none;color:transparent;caret-color:#fff;font-family:"Source Code Pro",monospace}.de-editor_wrapper .de-editor .de-editor_code.highlight{z-index:10;margin:0;pointer-events:none;font-family:"Source Code Pro",monospace}.de-editor_wrapper .de-editor .de-editor_code .de-editor_codeblock{position:absolute;max-width:100%;top:0;left:0;margin:0;padding:1rem;display:block;font-family:"Source Code Pro",monospace}
.de-files_container{display:flex;flex-direction:row;font-family:"Source Code Pro",monospace}.de-files_container .de-file{flex:1 1;cursor:pointer;padding:8px;background-color:#0e0e0e;color:#fff;border-right:#272822 4px solid;box-sizing:border-box;display:flex;align-items:center;justify-content:flex-start}.de-files_container .de-file>svg{margin-right:4px}.de-files_container .de-file:hover{background-color:#202020}.de-files_container .de-file.active{background-color:#272822}
